import React from 'react';
import * as API from '../../api.json';
import { NavItem } from './nav/NavItem';

const getNavItems = () => {
  return Object.keys(API).map(key => {
    return <NavItem path="/docs/" name={API[key].name} />;
  });
};

export const Nav = () =>
  <nav>
    <ul className="col-xs-4">
      {getNavItems()}
    </ul>
  </nav>;



